.SigninPage {
  padding-top: 50px;

  .wrapper {
    display: flex;
  }

  .left-col {
    width: 30%;
    max-width: 400px;
    height: calc(100vh - 50px);
    background: #1abc9c;
    display: flex;
    align-items: center;
    border-right: 2px solid var(--wet-asphalt);
  }

  .right-col {
    width: 70%;
    display: flex;
    align-items: center;
  }


  .form-wrapper {
    max-width: 500px;
    width: 100%;
    margin: auto;
  }

  .title {
    text-align: center;
  }

  .label {
    margin-bottom: 0;
  }
  .submit {
    margin-top: 40px;
  }
  .input {


    &.error {
      border-color: #ff3013;
    }
  }
  .error-message {
    position: absolute;
    color: #ff3013;
    font-size: 12px;
  }
}
