.AppMenu {
  .bm-overlay {
    top: 0;
  }
  .bm-menu-wrap {
    top: 0;
    background: var(--wet-asphalt);
  }
  .bm-item-list {
    padding: 40px 20px;
    a {
      display: block;
      color: white;
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 10px;
    }
  }
  .bm-item {
    margin-bottom: 10px;
    svg {
      vertical-align: middle;
      margin-right: 5px;
    }
    span {
      vertical-align: middle;
    }
  }
  .bm-cross {
    background: white;
  }
  .bm-burger-button {
    position: fixed;
    width: 30px;
    height: 23px;
    left: initial;
    right: 25px;
    top: 14px;
  }
  .bm-burger-bars {
    background: var(--wet-asphalt);
  }

  ul.menu-items {
    padding-left: 10px;
    display: none !important;
    a {
      font-weight: 400;
    }
  }

  @media screen and (max-width: 1024px) {
    ul.menu-items {
      display: block !important;
    }
  }
}
