.SocialLogin {
  margin-bottom: 50px;
  max-width: 500px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

  button {
    width: 100%;
    margin-bottom: 10px;
    cursor: pointer;
    vertical-align: middle;
  }

  .fb-button {
    height: 48px;
    border-radius: 3px;
    //border: 1px solid #cccccc;
    border: none;
    background: white;
    text-align: left;
    font-weight: 500;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.54);
    font-family: Roboto, sans-serif;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 2px 0px, rgba(0, 0, 0, 0.24) 0px 0px 1px 0px;

    &:hover {
      opacity: 0.9;
    }

    svg {
      width: 28px;
      height: 28px;
      vertical-align: middle;
      margin-right: 15px;
      color: #4267b2;
    }
  }
}
