.SignupForm {
  max-width: 500px;
  margin: auto;
  width: 100%;

  &.place-modal {
    .SocialLogin {
      button {
        width: 49%;
        margin-right: 1%;
      }
    }
  }

  .signin {
    text-align: center;
  }

  .form-wrapper {
    max-width: 500px;
    width: 100%;
    margin: auto;
  }

  .title {
    text-align: center;
  }

  .control {
    margin-bottom: 15px;
  }

  .label {
    margin-bottom: 0;
  }
  .submit {
    margin-top: 40px;
  }
  .input {


    &.error {
      border-color: #ff3013;
    }
  }
  .error-message {
    position: absolute;
    color: #ff3013;
    font-size: 12px;
  }
}
