.ColorPicker {
  position: fixed;
  bottom: 0;
  background: white;
  //right: 0;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;

  .color {
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-right: 5px;
    margin-bottom: 5px;
    cursor: pointer;
    border: 1px solid var(--wet-asphalt);
    border-radius: 4px;
    vertical-align: middle;
  }

  .color-pipette {
    padding: 5px;
    svg {
      width: 100%;
      height: 100%;

      path {
        stroke: #ffffff;
        stroke-width: 8px;
      }
    }
  }

  .chromePicker {
    position: absolute;
    bottom: 75px;
  }
}

@media screen and (max-width: 768px) {
  .ColorPicker {
    width: 100%;
    background: white;
    left: 0;
    padding-top: 10px;

    .color {
      width: 25px;
      height: 25px;
      margin-right: 2px;
      margin-bottom: 2px;
    }
    .color-pipette {
      padding: 1px;
      overflow: hidden;
      top: 0;
    }
  }
}
