.download-modal {
  text-align: center;
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  max-width: 800px;
  max-height: 650px;
  margin: auto;
  background: rgba(52, 73, 94, 0.9);
  color: white;
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;

  > div {
    display: flex;
    justify-content: center;
    height: 100%;
  }

  .isDownloading {
    height: 100%;

    .email {
      margin-bottom: 100px;
    }

  }

  .spinner-wrapper {
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
  }
}
