.DownloadsPage {
  padding-top: 50px;
  color: white;


  .wrapper {
    padding: 20px;
    background: var(--wet-asphalt);
    min-height: calc(100vh - 50px);
    overflow: hidden;
  }

  .width-wrapper {
    max-width: 1024px;
    margin: auto;
  }

  .no-downloads {
    margin-top: 50px;
  }

  .title {
    margin-bottom: 30px;
  }

  .spinner-wrapper {
    width: 300px;
    height: 300px;
    overflow: hidden;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
  }

  .preview-tile {
    position: relative;
    display: inline-block;
    background-color: white;
    width: 150px;
    text-align: center;
    padding: 5px;
    border-radius: 4px;
    margin-right: 10px;
    margin-bottom: 10px;
    &.deleting {
      &:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: #333333;
        opacity: 0.6;
      }
    }

    .spinner {
      z-index: 1;
    }
  }

  button, a {
    margin-top: 2px;
  }
}
