.download-success-modal {
  text-align: center;
  position: absolute;
  top: 40px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  max-width: 800px;
  margin: auto;
  background: rgba(52, 73, 94, 0.9);
  color: white;
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;

  .modal-image {
    width: 350px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
}
