.Tile {
  display: inline-block;
  width: 150px;
  height: 150px;
  border: 1px solid var(--silver);
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
  border-radius: 4px;
  cursor: pointer;
  vertical-align: middle;

  &:hover {
    border-color: var(--wet-asphalt);
  }

  &.active {
    border-color: var(--wet-asphalt);
    //outline: 1px solid var(--wet-asphalt) !important;
    box-shadow: 0 0 1px 0 var(--wet-asphalt);
  }

}

@media screen and (max-width: 768px) {
  .Tile {
    width: 45%;
    height: auto;
    padding-top: 45%;
    margin-right: 2.5%;
    margin-left: 2.5%;
    margin-bottom: 5%;

    img {
      position: absolute;
      top: 0;
    }
  }
}
