.AppHeader {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 50px;
  background: white;
  border-bottom: 2px solid var(--wet-asphalt);
  display: flex;
  align-items: center;

  .logo {

    margin-left: 30px;
    font-size: 20px;
    text-transform: uppercase;

    a {
      color: #8E44AD;

      svg {
        margin-right: 10px;
      }
    }

  }

  .menu-items {
    margin-bottom: 0;
    margin-left: 80px;
    li {
      display: inline-block;
      margin-right: 50px;

      a {
        font-weight: 600;
        color: var(--wet-asphalt);
        opacity: 0.7;

        &.disabled {
          cursor: default;
          opacity: 1;
        }
      }
    }
  }

  .signup-wrapper, .profile-name  {
    margin-left: auto;
    padding-right: 120px;
  }

  @media screen and (max-width: 1024px) {
    .menu-items {
      display: none;
    }
    .signup-wrapper {
      display: none;
    }
  }
}

