.App {
}

.App-left-col {
  background: var(--wet-asphalt);
  flex: 5;
  max-width: 600px;
  height: calc(100vh - 50px);
  display: flex;
  justify-content: center;
  padding: 10px 20px;
  position: sticky;
  top: 50px;

  .btn-wide {
    width: 100%;
  }
}

.left-col-container {
  height: 100%;
  width: 100%;
}

.App-main {
  flex: 5;
  height: auto;
  padding-bottom: 150px;
}

.react-tabs {
  padding: 10px 10px;
}

.ReactModal__Overlay {
  z-index: 1000;
}

@media screen and (max-width: 768px) {
  .App-left-col {
    flex: 0 0 100%;
    max-width: none;
    height: auto;
    padding-bottom: 40px;
    top: auto;

    .left-col-container {
      height: auto;
    }
  }
  .App-main {
    flex: 0 0 100%;
    height: auto;
  }
}
